import React, { Component } from 'react'
import { Sparklines, SparklinesCurve } from 'react-sparklines';
import { bitcoinService } from '../../services/bitcoinService.js'

export class StatisticsPage extends Component {

    state = {
        price: null,
        transactions: null,
        isLoading: true
    }

    componentDidMount() {
        this.getStats()
    }

    async getStats() {
        const priceRaw = await bitcoinService.getMarketPrice()
        const transactionsRaw = await bitcoinService.getConfirmedTransactions()
        const priceModified = []
        const transactionsModified = []
        priceRaw.forEach(point => priceModified.push(point.y))
        transactionsRaw.forEach(point => transactionsModified.push(point.y))
        this.setState({ price: priceModified, transactions: transactionsModified, isLoading: false })
        console.log(this.state)
    }

    render() {
        return (
            !this.state.isLoading && <div className="statistics-page">
                <h2>Market Price (USD)</h2>
                <Sparklines data={this.state.price} width={100} height={20} margin={5}>
                    <SparklinesCurve color="#8e44af" />
                </Sparklines>
                <p>Average USD market price across major bitcoin exchanges.</p>

                <h2>Confirmed Transactions per Day</h2>
                <Sparklines data={this.state.transactions} width={100} height={20} margin={5}>
                    <SparklinesCurve color="#41c3f9" />
                </Sparklines>
                <p>The number of daily confirmed bitcoin transactions.</p>
            </div>
        )
    }
}