import React, { Component } from 'react'
// import { connect } from 'react-redux'
import userService from '../../services/userService.js'
import {bitcoinService} from '../../services/bitcoinService.js'
import coinsIcon from '../../assets/icons/coins.png'
import BTCIcon from '../../assets/icons/bitcoin.png'

export class HomePage extends Component {

    state = {
        rate: null,
        isLoading:true
    }

    componentDidMount(){
        this.getRate()
    }

    async getRate(){
        const rate = await bitcoinService.getRate()
        this.setState({rate, isLoading:false})
    }
    
    render(){
        const user = userService.getUser()
        return (
            !this.state.isLoading && <div className="homepage">
                <h1>Hello, {user.name}!</h1>
                <h2><img src={coinsIcon} alt="Coins" /> Coins: {user.coins}</h2>
                <h2><img src={BTCIcon} alt="BTC" /> BTC: {this.state.rate}</h2>
            </div>
        )
    }
}