import { storageService } from './storageService.js'
const axios = require('axios');
const CACHE_TIME = 86400000; //24 Hours


export const bitcoinService = {
    getRate, getMarketPrice, getConfirmedTransactions
}

function getRate() {
    let rate = storageService.load('rate')
    const lastCachedAt = storageService.load('rateTimestamp')
    if ((!rate) || Date.now() - lastCachedAt > CACHE_TIME) {
        axios.get(`https://blockchain.info/tobtc?currency=USD&value=1`)
            .then(res => res.data)
            .then((data) => {
                rate = data
                storageService.store('rate', rate)
                storageService.store('rateTimestamp', Date.now())
            })
    }
    return Promise.resolve(rate)
}

function getMarketPrice() {
    let price = storageService.load('price')
    const lastCachedAt = storageService.load('priceTimestamp')
    if ((!price) || Date.now() - lastCachedAt > CACHE_TIME) {
        axios.get(`https://api.blockchain.info/charts/market-price?timespan=5months&format=json&cors=true`)
            .then(res => res.data)
            .then((data) => {
                price = data.values
                console.log(price)
                storageService.store('price', price)
                storageService.store('priceTimestamp', Date.now())
            })
    }
    return Promise.resolve(price)
}

function getConfirmedTransactions() {
    let transactions = storageService.load('transactions')
    const lastCachedAt = storageService.load('transactionsTimestamp')
    if ((!transactions) || Date.now() - lastCachedAt > CACHE_TIME) {
        axios.get(`https://api.blockchain.info/charts/n-transactions?timespan=5weeks&format=json&cors=true`)
            .then(res => res.data)
            .then((data) => {
                transactions = data.values
                storageService.store('transactions', transactions)
                storageService.store('transactionsTimestamp', Date.now())
            })
    }
    return Promise.resolve(transactions)
}